@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-webp .horario {
    background-image: linear-gradient( to bottom, v.$rojoclaro 0%, v.$rojoclaro 50%, transparent 50% ), url(../img/bg_horarios.png);
    @include m.tablet { 
        background-image: linear-gradient( to right, v.$rojoclaro 0%, v.$rojoclaro 53%, transparent 53% ), url(../img/bg_horarios.png);
    }
}
.webp .horario {
    background-image: linear-gradient( to bottom, v.$rojoclaro 0%, v.$rojoclaro 50%, transparent 50% ), url(../img/bg_horarios.webp);
    @include m.tablet { 
        background-image: linear-gradient( to right, v.$rojoclaro 0%, v.$rojoclaro 53%, transparent 53% ), url(../img/bg_horarios.webp);
    }
}

.horario {    
    background-repeat: no-repeat;
    background-position: center, bottom center;
    color: v.$blanco;
    @include m.tablet { 
        background-position: center, top right;
    }
    &__grid {
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        @include m.tablet { 
            grid-template-rows: unset;
            @include m.grid(2, 1rem);
        }
        @include m.desktop { 
            grid-template-rows: unset;
            @include m.grid(2, 5rem);
        }
    }
    &__contenido {
        padding: 1rem 0;

        @include m.tablet {
            padding: 2rem 0;
        }

        @include m.desktop {
            padding: 5rem 0;
        }
    }
    &__heading {
        @include m.heading;
        font-weight: 300;
        font-size: 3rem;
 
        @include m.desktop {
            font-weight: 400;
            font-size: 4rem;         
        }
    }
    &__texto {
        font-size: 1.8rem;
        line-height: 1.5;
        text-align: center;

        @include m.tablet {
            font-size: 2rem;
            line-height: 1.6;
        }

        @include m.desktop {
            font-size: 2.2rem;
            line-height: 1.8;
        }
    }
}
.tabla {
    width: 98%;
    border: .5rem solid v.$amarillo;
    text-align: center;
    border-collapse: collapse;
    &__thead {
        background-color: v.$amarillo;
        
    }
    &__th {
        padding: 1rem;
        font-size: 1.7rem;
        @include m.tablet {
            font-size: 2rem;
        }        
        @include m.desktop {
            padding: 2rem;
            font-size: 2.2rem;
        }
    }
    &__tr {        
        &:hover {
            background-color: darken(v.$naranja, 10%);
        }
    }
    &__td {
        padding: 1rem;
        font-size: 1.7rem;

        @include m.desktop {
            font-size: 2rem;
        }
    }
}