@use 'variables' as v; 
@use 'mixins' as m;
.cookies {
  width: 100%;
  position: fixed;
  bottom: 0;
  color: v.$blanco;
  background-color: v.$grisOscuro;
  z-index: 9999;
  display: none;

    &__contenido {
      min-height: 7rem;
      display: flex;
      justify-content:space-around;
      align-items: center;
      flex-wrap: wrap;
      gap: 1rem;
      padding: 2rem 0;

      @include m.tablet { 
          padding: 1rem;
      }
    }
    &__enlace {
      color: v.$amarillo;
      font-weight: 500;
      text-decoration: none;
    }
}